/* left side section */
.intro {
  display: flex;
  height: 77vh;
  margin-top: 6rem;

}
.i-left {
  display: flex;
  position: relative;
  flex: 1;
  flex-direction: column;
  gap: 2rem;
}
.i-name {
  display: flex;
  flex-direction: column;
}
.i-name > :nth-child(1) {
  color: var(--black);
  font-weight: bold;
  font-size: 2rem;
}
.i-name > :nth-child(2) {
  color: var(--orange);
  font-weight: bold;
  font-size: 2.5rem;
}
.i-name > :nth-child(3) {
  font-weight: 100;
  font-size: 14px;
  color: var(--gray);
  margin-top: 10px;
}
.i-button {
  width: 6rem;
  height: 2rem;
}
.i-icons {
  display: flex;
  gap: 1rem;
  margin-top: 2rem;
}
/* social media image left side */
.i-icons > a > img {
  width: 2rem;
  height: 2rem;
}
 /* left side section exit */

/* right side section */
.i-right {
  flex: 1;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.i-right > img {
  width: 17rem;
  height: 20rem;
  position: absolute;
  top: 2rem;
  right: 1rem;
}
.i-right > :nth-child(4) {
  transform: scale(0.5);
  top: -30%;
  border-radius: 50%;
  margin-left: 50px;
}
.blur {
  position: absolute;
  width: 10rem;
  height: 14rem;
  border-radius: 50%;
  background: #edd0ff;
  filter: blur(212px);
  z-index: -9;
  filter: blur(72px);
}
/* thumbup image */
.thumbup {
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: absolute;
  margin-top: -2.5px;
  margin-left: -130px;
  background-color: #f5f5f5;
  border-radius: 10px;
}
.thumbup > span {
  color: var(--orange);
  font-weight: bold;
  font-family: sans-serif;
  font-size: 16px;
  margin-left: 13px;
  margin-right: 10px;
  width: 5rem;
  height: 3.2rem;
}
.thum {
  transform: scale(1.3);
  width: 2.3rem;
  height: 2.3rem;
  margin-left: 5px;
}
/* Media Queries */

/* Tablets and small desktops */
@media (max-width: 768px) {
  .intro {
    flex-direction: column;
    height: auto;
    margin-top: 4rem;
  }

  .i-left,
  .i-right {
    flex: none;
    align-items: center;
    justify-content: center;
  }

  .i-left {
    margin-bottom: 2rem;
  }
}

/* Mobile devices */
@media (max-width: 480px) {
  .intro {
    flex-direction: column;
    height: auto;
    margin-top: 3.5rem;
  }

  .i-name > :nth-child(1) {
    font-size: 1.5rem;
  }
  .glassesimoji {
    display: none;
  }

  .i-name > :nth-child(2) {
    font-size: 1.5rem;
  }

  .i-name > :nth-child(3) {
    font-size: 15px;
  }

  .i-button {
    width: auto;
    padding: 0.5rem 1.5rem;
    font-size: 12px;
  }

  .i-icons {
    margin-top: 1.5rem;
  }

  /* Card animation */
  .i-right {
    animation: slideIn 0.5s ease-in-out;
  }

  @keyframes slideIn {
    from {
      transform: translateX(100%);
      opacity: 0;
    }
    to {
      transform: translateX(0);
      opacity: 1;
    }
  }
}
