/* Testimonial component */

.t-wrapper {
  padding: 0 3rem 0 3rem;
  height: 100vh;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 2rem;
  margin-top: 7rem;
  position: relative;
}

.t-heading {
  align-self: start;
}

.t-heading>* {
  font-size: 2rem;
  font-weight: bold;
}

.t-heading span:nth-of-type(2) {
  color: var(--orange);
}

.t-blur1 {
  left: 38rem;
  top: 16px;
}

.t-blur2 {
  top: 9rem;
  left: 0;
}

.testimonial {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
  margin-top: 5rem;
  padding: 2rem;
  width: 30rem;
  height: 15rem;
  border-radius: 20px;
  background: rgba(255, 255, 255, 0.26);
  border: 7px solid var(--blueCard);
  box-shadow: var(--boxShadow);
}

.testimonial>img {
  width: 6rem;
  height: 6rem;
  border-radius: 100%;
}

.testimonial>span {
  color: var(--gray);
}

.t-wrapper .swiper {
  width: 100%;
}

.t-wrapper .swiper-slide {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 2rem;
}

.t-wrapper .swiper-pagination-bullet-active {
  background: var(--orange);
}

/* Media Queries */

/* Tablets and small desktops */
@media (max-width: 768px) {
  .t-wrapper {
    padding: 0 2rem;
    margin-top: 5rem;
  }

  .t-heading>* {
    font-size: 1.5rem;
  }

  .testimonial {
    width: 25rem;
    height: 13rem;
  }

  .testimonial>img {
    width: 5rem;
    height: 5rem;
  }
}

/* Mobile devices */
@media (max-width: 480px) {
  .t-wrapper {
    padding: 0 1rem;
    margin-top: 5rem;
   
  }

  .t-heading>* {
    font-size: 1.5rem;
    margin-top: 6rem;
    
  }

  .testimonial {
    width: 20rem;
    height: 20rem;
  
  }

  .testimonial>img {
    width: 4rem;
    height: 4rem;
    
  }
}
